@import url('https://fonts.googleapis.com/css2?family=Gabriela&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;


@layer components{
    .bannerIcon{
        @apply w-16 h-16 bg-black bg-opacity-25 text-gray-200 text-xl inline-flex
        items-center justify-center rounded-md  hover:bg-opacity-40
        hover:-translate-y-1 transition-all hover:text-designColor cursor-pointer
        duration-300;
    }
    .resumeLi{
        @apply w-full h-20 bg-black bg-opacity-25 text-xl text-gray-300 flex 
        justify-center cursor-pointer hover:bg-opacity-40 duration-300 items-center
        border-[1px] rounded-none border-none;
    }
    .bgOpacity{
        @apply bg-black bg-opacity-40;
    }
}


.active{
    color:white;
}